<template>
    <div class="form-prices">
        <div class="h2">Найближчі старти <br>LIFESTYLE груп</div>
        <div class="form-prices-body">
            <div
                    v-for="(item, index) in groups"
                    :key="index"
                    class="form-prices-item">
                <div class="form-prices-item--head">
                    {{ item.level_title }}
                </div>
                <div class="form-prices-item--body">
                    <div class="form-prices-item--time">
                        <div class="--title">Стартуємо: <span>{{ getStartDate(item.start_date) }}</span></div>
                        <div class="--body">{{ getStartTime(item.time) }}</div>
                        <div class="--subtitle">
                            <span v-for="(day, key) in item.days" :key="key">
                                {{ daysList[day].title_ua }}<template v-if="key < item.days.length - 1">, </template>
                            </span>
                        </div>
                    </div>
                    <ul>
                        <li>
                            <i><img src="../../assets/images/icons.svg#icon-14" alt="#"></i>
                            <p>Розмовний клуб з носіями мови</p>
                        </li>
                        <li>
                            <i><img src="../../assets/images/icons.svg#icon-14" alt="#"></i>
                            <p>WinTalk та Winletter включені в курс</p>
                        </li>
                        <li>
                            <i><img src="../../assets/images/icons.svg#icon-14" alt="#"></i>
                            <p>#OnlyEnglish - тільки англійська</p>
                        </li>
                        <li>
                            <i><img src="../../assets/images/icons.svg#icon-14" alt="#"></i>
                            <p>Тривалість семестру: 3 місяці</p>
                        </li>
                    </ul>
                </div>
                <div class="form-prices-item--footer">
                    <div class="form-prices-item--footer--before">
                        <i></i>
                        <i></i>
                        <i></i>
                        <i></i>
                        <i></i>
                        <i></i>
                        <i></i>
                    </div>
                    <div class="--title">Семестр занять за програмою:</div>
                    <div class="--body">
                        <div class="price" :class="{'old-price' : item.discount_price > 0}">
                            {{ item.price }}<span>грн.</span>
                            <i v-if="item.discount_price > 0"><img src="../../assets/images/layers.svg#layer-23" alt="#"></i>
                        </div>
                        <div v-if="item.discount_price > 0" class="discount-price">{{ item.discount_price }}</div>
                    </div>


                    <div class="--buttons">
                        <div class="btn-box">
                            <div class="btn btn-1" @click="openModal(item)">
                                Дізнайся свій рівень
                            </div>
                        </div>
                        <a href="#">Повний розклад цього курсу</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        getGroup
    } from '../../api';
    import { mapActions } from 'vuex';
    import { daysList } from '../../const/daysList';
    import { formMixin } from '../../mixins/form.mixin';
    export default {
        mixins: [formMixin],
        props: ['propItems'],
        data() {
            return {
                data: {
                    "selected": 'underwater',
                    "full_title": '',
                    "group" : null
                },
                groups: null,
                daysList
            };
        },
        methods: {
            ...mapActions('modals', ['openBaseModal']),
            openModal(item) {
                this.data.data = item;
                this.data.data.full_title = item.level_title;
                this.openBaseModal(this.data);
            },
            filterData(data) {
                return data.filter(obj => {
                    return ((new Date(obj['start_date']).getTime()) > new Date().getTime())
                });
            }
        },
        created() {
            const params = {
                course: 'lifestyle',
                type: 'adults'
            };
            getGroup(params).then(
                result => {
                    this.groups = this.filterData(result).slice(0, this.propItems).map(
                        el => ({...el, days: JSON.parse(el.days)}));
                },
                error => {
                    console.warn(error)
                }
            );
        },
        mounted() {
        }
    }
</script>
<style lang="scss">
    @import "../../sass/components/form";
</style>
